import React, { useEffect, useState } from "react";
import "bootstrap/dist/css/bootstrap.css";
import StudentA from "../components/StudentA";
import StudentB from "../components/StudentB";

export default function Reports() {
  const [stud, setStud] = useState("Select Student");

  const [studentAVisible, setStudentAVisible] = useState(false);
  const [studentBVisible, setStudentBVisible] = useState(false);

  useEffect(() => {
    stud === "Student A" ? setStudentAVisible(true) : setStudentAVisible(false);
    stud === "Student B" ? setStudentBVisible(true) : setStudentBVisible(false);
  }, [stud]);

  const handleOnChange = (e) => {
    setStud(e.target.value);
  };

  const makeFistLetterCapital = (str) => {
    return str.charAt(0).toUpperCase() + str.slice(1);
  };

  const renderResult = () => {
    let result;
    stud === "Select Student"
      ? (result = "")
      : (result = makeFistLetterCapital(stud) + " - Class Report");
    return result;
  };

  return (
    <div className="container">
      <div class="row">
        <h3> Class Report</h3>
        <h5> Class Attention </h5>
        <h1> Image </h1>
        <p>
          {" "}
          Well Done! <b>91%</b> of the class was attentive during teaching.{" "}
        </p>
      </div>
      <div class="row">
        <div class="col-md-12">
          <h3> Student Report </h3>
        </div>
        <div class="col-md-12">
          <label>Student Name</label>
          <select
            class="form-select"
            style={{ width: "80vw" }}
            value={stud}
            onChange={handleOnChange}
          >
            <option value="Select Student">Select Student</option>
            <option value="Student A">Student A</option>
            <option value="Student B">Student B</option>
          </select>

          <br />
          <button type="button" class="btn btn-outline-primary">
            Get Report
          </button>
        </div>
        <div class="col-md-12">
          {renderResult()}
          {studentAVisible && <StudentA />}
          {studentBVisible && <StudentB />}
        </div>
      </div>
    </div>
  );
}
