import React, { useState } from "react";
import "./Topic.css";
import Logo from "../components/logo";
import "bootstrap/dist/css/bootstrap.css";
import Data from "./TopicData.js";
export default function Topic() {
  // const [show, setShow] = useState(false);
  const [inp, setInp] = useState("");
  const [searchVal, setSearchVal] = useState(inp);
  const inputChange = (event) => {
    console.log(event);
    setInp(event.target.value);
  };
  const handleClick = () => {
    setSearchVal(inp);
  };
  return (
    <div className="home">
      <div className="container col1">
        <div className="img1">
          <Logo />
        </div>
        <p class="head1"> Whirlykop </p>
        <p class="title1"> Welcome to Whirlykop, please search for a topic. </p>
        <div class="container">
          <div class="row">
            <center>
              <div class="col-sm-6">
                <input
                  className="form-control"
                  type="text"
                  name="name"
                  placeholder="Enter Topic To Search"
                  onChange={inputChange}
                />
                <br />
                <button class="btn btn-outline-danger" onClick={handleClick}>
                  {" "}
                  Search{" "}
                </button>
              </div>
            </center>
          </div>
          <br />
          <div class="row">
            {Data.filter((val) => {
              if (inp === "") {
                return null;
              } else if (val.topic.toLowerCase().includes(inp.toLowerCase())) {
                return val;
              }
            }).map((val, key) => {
              return (
                <div class="col-lg-4">
                  <iframe
                    key={key}
                    width="80%"
                    height="200"
                    src={val.src}
                    title={val.title}
                    frameborder="10"
                    allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                    allowfullscreen
                  ></iframe>
                </div>
              );
            })}
          </div>
        </div>
      </div>
    </div>
  );
}
